import React, { useState, useEffect } from 'react';
import { Link, } from "react-router-dom";
import axios from 'axios';
import Papa from 'papaparse';

function Recruitment() {
    const [positions, setPositions] = useState([]);


    useEffect(() => {
        axios.get("/data/recruitment.csv")
            .then(({ data }) => {
                console.log(data)
                const d = Papa.parse(data, {header: true})
                setPositions(d.data);
            });
    }, []);


    return (
        <div id="site_content">
            <div id="content">
            <h1>Recruitment</h1>
                <ul>
                    {positions.map(position => (
                        <li key={position.ref_no}>
                            <a href={position.url} target="_blank" rel="noopener noreferrer"> {position.title} </a> (Closing Date: {position.closing_date})
                        </li>
                    ))}
                </ul>

            </div>
        </div>
    );
}

export default Recruitment;
